import React from 'react';
import Button from '../button/button.component';
import Paragraph from '../paragraph/paragraph.component';
import icone1 from '../../images/Emoticon/icons8-facebook-logo-50.png';
const Menu = () => {
    const farstMenu = [
        { title: 'خونه' },
        { title: 'کمدا چیه؟' },
        { title: 'چرا کمدا؟' },
        { title: 'چه شکلیه؟' },
        { title: 'قوانین و مقررات' },
        { title: 'تعریف و تمجید' },
        { title: 'سوال پیچ' },
        { title: 'پیدامون کن!' },
        { title: 'درباره ما' },
        { title: 'تماس با ما' },
        { title: 'شکایت' },
    ];

    return (
        <div className="row p-3 w-100">
           <div className="col-12 col-md-1 text-center">
                <img src={icone1} alt='logo' />
            </div>
            <div className="col-12 col-md-7 d-flex flex-wrap mt-2 justify-content-center">
                {farstMenu.map(item => {
                    return (
                        <Paragraph title={item.title} className='text-light fs-6 ms-3' />
                    )
                })}
            </div>
           <div className="col-12 col-md-4">
            <div className="d-flex justify-content-end gap-2">
                <Button className='p-buttonmenu' title='رادیو مدا' />
                <Button  className='p-buttonmenu'title='فروشگاه' />
                <Button className='p-buttonmenu' title='دانلود اپلیکشین' />
            </div>
            </div>
        </div>
    );
};

export default Menu;